export default {
	data() {
		return {
			signName: '', //姓名
			signNum: '', //电话
			signHospatel: '', //医院
			signOffeice: '', //科室
			token: '',
			disabledBtn:true
		}
	},
	onLoad() {
		var that = this;
		var serverUrl = that.serverUrl
		uni.getStorage({
			key: 'token',
			success: function(res) { //成功
				that.token = uni.getStorageSync('token');
				uni.request({
					url: serverUrl + '/memberDetail/select',
					method: "GET",
					header: {
						'Content-Type': 'application/x-www-form-urlencoded',
						"token": that.token
					},
					success: (res) => {
						if (res.data.code == 20000) {
							var memberName = res.data.result.memberName;
							if (memberName != null) {
								that.signName = res.data.result.memberName;
								that.signNum = res.data.result.telephone;
								that.signHospatel = res.data.result.hospitalName;
								that.signOffeice = res.data.result.branchName;
								that.disabledBtn = false;
							}else {
								that.signName = '';
								that.signNum = '';
								that.signHospatel = '';
								that.signOffeice = '';
								that.disabledBtn = true;
							}
						} 
					},
				})
			},
			fail(res) {
				uni.redirectTo({
					url: "../Authorization/Authorization"
				})
			}
		});


	},
	methods: {
		checkMobile(mobile) {
			return RegExp(/^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/).test(mobile);
		},
		succeed() {
			if (this.signName == '') {
				uni.showToast({
					title: "名字不能为空	",
					icon: 'none'
				})
			} else if (this.signHospatel == '') {

				uni.showToast({
					title: "医院不能为空",
					icon: 'none',
				})
			} else if (this.signOffeice == '') {

				uni.showToast({
					title: "科室不能为空",
					icon: 'none',
				})
			} else if (this.signNum == '') {

				uni.showToast({
					title: "手机号不能为空",
					icon: 'none',
				})
			} else if (!this.checkMobile(this.signNum)) {
				uni.showToast({
					title: "手机号格式不正确",
					icon: 'none',
				})
			} else {
				var that = this;
				var serverUrl = that.serverUrl
				that.token = uni.getStorageSync('token');
				uni.request({
					url: serverUrl + '/memberDetail/update',
					method: "POST",
					data: {
						memberName: that.signName,
						telephone: that.signNum,
						hospitalId: that.signHospatel,
						branchId: that.signOffeice,
					},
					header: {
						'Content-Type': 'application/x-www-form-urlencoded',
						"token": that.token
					},
					success: (res) => {
						console.log(res)
						if (res.data.code == 20000) {
							uni.showToast({
								title: "修改成功",
								icon: 'success',
							})
						} else {
							uni.showToast({
								title: "修改失败",
								icon: 'none',
							})
								uni.redirectTo({
								url: "../Authorization/Authorization"
							})
						}
					},
				})
			}
		},
	},
}
